<template>
	<a href="https://sekai.best/chara/25" target="_blank" class="nonunderline">
		<img src="https://sekai.best/assets/chr_ts_25.83d109af.png" class="meiko" style="width:100%;" alt="MEIKO" :onerror="headimg"/>
	</a>
</template>

<script>
import meikohead from '@/assets/meikohead.png'

export default {
	name: 'meiko',
	data() {
		return {
			headimg: "this.src='" + meikohead + "';this.onerror=null;"
		}
	}
}
</script>



