<template>
	<a href="https://sekai.best/chara/22" target="_blank" class="nonunderline">
		<img src="https://sekai.best/assets/chr_ts_22.4ab35893.png" class="rin" style="width:100%;" alt="镜音铃" :onerror="headimg"/>
	</a>
</template>

<script>
import rinhead from '@/assets/rinhead.png'
export default {
	name: 'rin',
	data() {
		return {
			headimg: "this.src='" + rinhead + "';this.onerror=null;"
		}
	}
}
</script>



