<template>
	<a href="https://sekai.best/chara/15" target="_blank" class="nonunderline">
		<img src="https://sekai.best/assets/chr_ts_15.06a049a5.png" class="nene" style="width:100%;" alt="草薙宁宁" :onerror="headimg"/>
	</a>
</template>

<script>
import nenehead from '@/assets/nenehead.png'
export default {
	name: 'nene',
	data() {
		return {
			headimg: "this.src='" + nenehead + "';this.onerror=null;"
		}
	}
}
</script>



