<template>
	<a href="https://sekai.best/chara/13" target="_blank" class="nonunderline">
		<img src="https://sekai.best/assets/chr_ts_13.003213bc.png" class="tms" style="width:100%;" alt="天马司" :onerror="headimg"/>
	</a>
</template>

<script>
import tmshead from '@/assets/tmshead.png'
export default {
	name: 'tms',
	data() {
		return {
			headimg: "this.src='" + tmshead + "';this.onerror=null;"
		}
	}
}
</script>



