<template>
  <div class="homepage">
    <div class="backgroundimg"></div>
    <div class="homepagetop">
      <div class="siteiconline" width="100%" height="10%">
        <div class="siteicontextdiv">
          <span id="siteicontextspan">laoshengji</span>
        </div>
      </div>
      <div class="homepagetopnav">
        <ul class="homepagetopnavul">
          <li :class="ispage(page,'homepage')" class="homepagetopnavele" @click.stop="page='homepage'">
            首页
          </li>
          <li :class="ispage(page,'colorful_stage')" class="homepagetopnavele" @click.stop="page='colorful_stage'">
            彩色舞台
          </li>
          <li :class="ispage(page,'about')" class="homepagetopnavele" @click.stop="page='about'">
            关于二次元
          </li>
        </ul>
      </div>
    </div>
    
    <div class="homepagecontent">
      <div v-show="page === 'homepage'">
        <homepagecontent/>
      </div>
      <div v-show="page === 'colorful_stage'">
        <colorfulstage/>
        
      </div>
      <div v-show="page === 'about'">
        <about/>
      </div>
    </div>
  </div>
</template>


<script>
import colorfulstage from '@/components/colorful_stage/colorful_stage.vue'
import homepagecontent from '@/components/homepagecontent.vue'
import about from '@/components/about.vue'
export default {
  components: { colorfulstage,homepagecontent,about},
  name: 'homepage',
  data() {
    return {
      page: 'homepage',
      classobject: {
        homepagetopnavelechoose: true
      }
    }
  }, 
  methods: {
    ispage(page,name) {
      return {
        homepagetopnavelechoose:page===name
      }
    }
  }
}
</script>

<style scoped>
.homepagetop {
  padding-left: 9%;
  padding-right: 5%;
  padding-top: 20px;
  padding-bottom: 10px;
}
.backgroundimg {
  position: fixed;
  top: 0; 
  left:0; 
  width: 100%; 
  height: 100%; 
  background-image: url("https://images.weserv.nl/?url=http://img.moegirl.org.cn/common/6/64/Card_233_trained.png");
  background-color: rgb(204, 103, 162);
  background-size: cover;
  opacity: 0.25;
  z-index: -1;
}
/* 网站标志行 */
.siteiconline {
  display:flex;
  flex-direction: row;
  flex-wrap:nowrap;
}
/* 网站标志元素 */
.siteicontextdiv {
  color: rgb(121, 253, 127);
  font-family: 'segoe print';
  font-size: 30px;
  background-color:rgb(253, 131, 243) ;
  text-align: left;
  width: auto;
  height: auto;
  padding-left:10px;
  padding-right:10px;
  border-radius: 50px;
  box-shadow: cyan 0px 0px 3px 3px;
  text-shadow: 2px 2px 2px rgb(1, 179, 39);
}
/* 网站标志文本 */
#siteicontextspan {
  font-weight:1000;
}

.homepagetopnav {
  margin-top: 20px;
  width:auto;
  background-color: lightpink;
}
.homepagetopnavul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  list-style-type:none;
  margin:0;
  padding:0;
}
.homepagetopnavele {
  white-space:nowrap;
  padding-left: 13px;
  padding-right: 13px;
  padding-top: 10px;
  padding-bottom: 10px;
  width: auto;
  font-weight: 600;
}
.homepagetopnavele:hover {
  background-color:#fb7299;
  color: white;
}
.homepagetopnavelechoose {
  border-bottom: solid rgb(248, 38, 38) 4px ;
  background-color:rgb(240, 142, 156);
}

.homepagecontent {
  padding-left: 8%;
  padding-right: 8%;
  padding-top: 10px;
  padding-bottom: 10px;
}
</style>


