<template>
	<a href="https://sekai.best/chara/21" target="_blank" class="nonunderline">
		<img src="https://sekai.best/assets/chr_ts_21.024fabcc.png" class="miku" style="width:100%;" alt="初音未来" :onerror="headimg"/>
	</a>
</template>

<script>
import mikuhead from '@/assets/mikuhead.png'
export default {
	name: 'miku',
	data() {
		return {
			headimg: "this.src='" + mikuhead + "';this.onerror=null;"
		}
	}
}
</script>



