<template>
	<a href="https://sekai.best/chara/24" target="_blank" class="nonunderline">
		<img src="https://sekai.best/assets/chr_ts_24.5b4a2497.png" class="luka" style="width:100%" alt="巡音一游" :onerror="headimg"/>
	</a>
</template>

<script>
import lukahead from '@/assets/lukahead.png'
export default {
	name: 'luka',
	data() {
		return {
			headimg: "this.src='" + lukahead + "';this.onerror=null",
		}
	}
}
</script>


