<template>
	<a href="https://sekai.best/chara/16" target="_blank" class="nonunderline">
		<img src="https://sekai.best/assets/chr_ts_16.00b69576.png" class="rui" style="width:100%;" alt="神代类" :onerror="headimg"/>
	</a>
</template>

<script>
import ruihead from '@/assets/ruihead.png'
export default {
	name: 'rui',
	data() {
		return {
			headimg: "this.src='" + ruihead + "';this.onerror=null;"
		}
	}
}
</script>



