<template>
	<a href="https://sekai.best/chara/14" target="_blank" class="nonunderline">
		<img src="https://sekai.best/assets/chr_ts_14.38de5874.png" class="emu" style="width:100%;" alt="凤绘梦" :onerror="headimg"/>
	</a>
</template>

<script>
import emuhead from '@/assets/emuhead.png'
export default {
	name: 'emu',
	data() {
		return {
			headimg: "this.src='" + emuhead + "';this.onerror=null",
		}
	}
}
</script>


